import React, { useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
// styles
import styles from "./SuccessPaymentBlock.module.scss";
// icons
import smileFace from "../../assets/img/cart_happy_face_emoji_emotion_smile_icon.svg";
// utils
import getRandomOrder from "../../utils/getRandomOrderNumber";
// components
import BackButton from "components/UI/BackButton";
// redux toolkit
import { useDispatch, useSelector } from "react-redux";
import { clearItems } from "../../redux/cart/slice";
import { selectCart } from "redux/cart/selectors";
// types
import { userSliceState } from "redux/user/types";
// hooks
import { useAuth } from "hooks/useAuth";

const SuccessPaymentBlock: React.FC = () => {
  const randomOrder = getRandomOrder(1, 100);
  const dispatch = useDispatch();
  const { totalPrice } = useSelector(selectCart);
  const { pathname } = useLocation();
  const { isAuth } = useAuth();
  const [coinsForOrder, setCoinsForOrder] = useState(() => {
    const coinsFromStorage = localStorage.getItem("coinsForOrder");
    return coinsFromStorage ? JSON.parse(coinsFromStorage) : 0;
  });
  const [orderNumber, setOrderNumber] = useState(() => {
    const orderNumberFromStorage = localStorage.getItem("orderNumber");
    return orderNumberFromStorage
      ? JSON.parse(orderNumberFromStorage)
      : randomOrder;
  });
  const navigate = useNavigate();
  const [didMount, setDidMount] = useState(false);

  useEffect(() => {
    console.log(didMount);

    console.log("render");

    if (pathname === "/success_payment") {
      if (didMount) {
        navigate("/");
      } else {
        dispatch(clearItems());
        if (!isAuth) {
          localStorage.removeItem("cart");
        } else {
          console.log("рендер бонусов");
          console.log(coinsForOrder);

          const currentUser = JSON.parse(localStorage.getItem("currentUser"));
          setCoinsForOrder((prevCoins) => {
            const newCoins = prevCoins + Math.round(totalPrice / 100);
            localStorage.setItem("coinsForOrder", String(newCoins));
            return newCoins;
          });

          setOrderNumber(() => {
            const newOrder = randomOrder;
            localStorage.setItem("orderNumber", JSON.stringify(newOrder));
            return newOrder;
          });

          currentUser.coins += Math.round(totalPrice / 100);
          currentUser.cart = [];
          localStorage.setItem("currentUser", JSON.stringify(currentUser));

          const userEmail = currentUser.email;
          const users = JSON.parse(localStorage.getItem("users"));
          const updatedUsers = users.map((user: userSliceState) => {
            if (user.email === userEmail) {
              user.cart = [];
            }
            return user;
          });

          localStorage.setItem("users", JSON.stringify(updatedUsers));
        }
        setDidMount(true);
      }
    }
  }, []);

  return (
    <h2 className={styles.root}>
      <div>
        <img src={smileFace} alt="smile_face" />
      </div>
      <div>Ура! Оплата прошла успешно!</div>
      <div>
        Номер вашего заказа <span>#{orderNumber}</span>
      </div>
      <div>
        Вы получили монет: <span>{coinsForOrder}</span>
      </div>
      <div>Ваш заказ уже начинают готовить!</div>
      <div>
        <span>Приятного аппетита!</span>
      </div>
      <Link to="/">
        <BackButton />
      </Link>
    </h2>
  );
};

export default SuccessPaymentBlock;
